  // เชคค่าว่าง ใน ฟอร์ม
  formIsValid = () => {
    const inputPrice = { ...this.state.inputPrice }; // อื่นๆ

    let isGood = true;

    if (inputPrice.value === "") {
      inputPrice.isValid = false;
      inputPrice.message = "กรุณากรอก ราคา และรหัส";
      isGood = false;
    }

    if (!isGood) {
      this.setState({
        inputPrice,
      });
    }

    return isGood;
  };

  // Add Sumbit
  handleSubmit = (event) => {
    event.preventDefault();

    if (this.formIsValid()) {
      console.log("ok");
      const { inputPrice, startDate, endDate } = this.state;
      const PriceR = inputPrice.value;
      const startDateR = `วันผลิต : ${moment(startDate).format("DD/MM/YYYY")}`;
      const endDateR = `วันหมดอายุ :${moment(endDate).format("DD/MM/YYYY")}`;

      this.printPDF(PriceR, startDateR, endDateR);
    }
  };

